<template>
  <div class="content-body">
    <a-form :form="form" @submit="handleSubmit">
      <div class="body-box">
        <div class="body-title">
          <h1>&nbsp;&nbsp;&nbsp;&nbsp;</h1>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="body-module">
          <div class="login-panel">
            <!-- 1 -->

            <div class="login-panel-title">
              <div class="login-panel-title-01"></div>
              <div>
                <div class="login-panel-title-02"></div>
                <em class="login-panel-title-03"></em>
                <i class="login-panel-title-04"></i>
                <i class="login-panel-title-05"></i>
              </div>
            </div>

            <!-- 2 -->
            <a-form-item>
              <a-input
                auto-focus="auto-focus"
                placeholder="//8ID"
                autocapitalize="on"
                class="login-panel-user"
                v-decorator="[
                      'username',
                      {
                        rules:[{required: true,message: ''}]
                      }
                      ]"
              ></a-input>
            </a-form-item>
            <!-- 3 -->
            <a-form-item>
              <a-input
                placeholder=""
                type="password"
                class="login-panel-password"
                v-decorator="[
                      'userpass',
                      {
                        rules:[{required: true,message: ''}]
                      }
                      ]"
              ></a-input>
            </a-form-item>
            <!-- 4 -->

            <a-button type="primary" html-type="submit" class="login-panel-button-primary"></a-button>
            <!-- 5 -->
            <div class="login-panel-links">
              <router-link
                :to="{
                        name: 'registerOk'
                  }"
              ></router-link>
              <router-link :to="{
                        name: 'test'
                  }"></router-link>
              <router-link :to="{
                        name: 'register'
                  }"></router-link>
            </div>
            <!-- 6 -->
            <div class="login-panel-third">
              <div class="login-panel-third-01"></div>
              <img src="../assets/images/wechat.png" class="login-panel-third-wechat" />
              <img src="../assets/images/qq.png" class="login-panel-third-qq" />
            </div>
          </div>
        </div>
      </div>
    </a-form>
  </div>
</template>

<script>

export default {
  data() {
    return {
      form: this.form
    }
  },
  computed: {

    key() {
      return this.$route.path + Math.random()
    }
  },
  beforeCreate() { // vuebeforeCreate,form
    // console.log('')
    this.form = this.$form.createForm(this, {
      onFieldsChange: (_, changedFields) => { // 
        this.$emit('change', changedFields)
      },
      mapPropsToFields: () => {
        return {
          username: this.$form.createFormField({
            value: this.username
          })
        }
      },
      onValuesChange: (_, values) => {
        // console.log(this.form)
        // console.log(values)
      }
    })
  },
  mounted() {
    // console.log(this.$route.params)
    let _this = this // Vuethis
    _this.username = _this.$route.params.username
    // console.log(window.vm)
  },
  methods: {
    handleSubmit(e) { // 
      // console.log(this.form)
      e.preventDefault()
      this.form.validateFields((err, values) => { // 
        if (!err) { // 
          // 
          this.confirmLogin(values)
        }
      })
    },
    confirmLogin(values) { // ,token?
      console.log('sfsffsfsdf')
      const _this = this

      _this.axios(
        {
          url: '/login',
          method: 'post',
          data: {
            username: values.username,
            userpass: values.userpass,
            identity_type: 'account'
          },
          transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              //  
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(
          (response) => {
            // console.log('what happen')
            console.log(response)

            const token = response.data.token
            const { id, nickname, avatar } = response.data.data.user
            const { menu } = response.data.data
            // console.log(response.data.data)
            // 
            // console.log(_this.$store)
            _this.$store.dispatch('commitaddtoken', token)
            _this.$store.dispatch('commitaddmenu', menu)

            _this.$store.dispatch('commitadduserinfo', { id, nickname, avatar })
              .then(() => {
                console.log(_this.$store.getters.token)
                // 
                _this.$router.push({ name: 'about' })
              })
              .catch(() => {
                console.log('false')
              })
          })
        .catch((error) => {
          console.log(error)
        })
    },
    showModal() {
      this.visible = true
    },
    handleOk(e) {
      console.log(e)
      this.visible = false
    },
    handleCancel(e) {
      console.log(e)
      this.visible = false
    }

  }
}
</script>

